.contentBlock {
    margin-left: 25%;
    margin-right: 25%;
    padding-top: 250px;
    padding-bottom: 100px;
}

div#intro {
    color: var(--light-text)
}
div#intro h1 {
    font-size: 5ch;
    
}

.intro-paragraph {
    width: 500px;
    color: var(--text);
    line-height: 2.5ch;
}

.green {
    color: var(--green);
}

div#footer {
    height:50px;
    margin-top: 150px;
    padding-bottom: 50px;
}

div#footer p {
    color: var(--light-text);
    font-size: 1.5ch;
    text-align: center;
    margin-bottom: 25px;
}

h2 {
    font-size: 2.75ch;
    color: var(--light-text);
}

div#footer div#externals{
    display: flex;
    flex-wrap: wrap;
    position: relative;
    justify-content: center;
    text-justify: center;
}

div#externals a{
    width: 30px;
    height: auto;
    margin: 5px;
}



div#externals a svg {
    object-fit: contain;
    
    
    font-family: var(--font-sans);
    font-size: var(--fz-xl);
    line-height: 1;
    
    text-align: right;
    
    color: var(--light-text);

    stroke: var(--light-text);
    stroke-width: 1.5;
    stroke-linecap: round;
    stroke-linejoin: round;
    max-width: 100%;
    vertical-align: middle;
    fill: none;
    width: 25px;
    height: 25px;
}

div#externals a:hover svg {
    stroke: var(--green);
}

div#externals a.facebook_link {
    margin-left: 0px;
}



